import { IonIcon, IonItem, IonLabel, IonModal, IonButton, IonText } from '@ionic/react'
import {  flowerOutline} from 'ionicons/icons';
import React, { useState } from 'react';
import './CoffeeControlChart.css'

function WECharacTour() {
    const [showModal, setShowModal] = useState(false);

    return(
        <IonItem>
         <IonIcon  icon={flowerOutline} slot="start" className = "ion-icon"/>
            <IonLabel>Multi-Dimensional Analysis of Iris with PCA and t-SNE</IonLabel>

         <IonModal isOpen={showModal} className = "ion-modal-popup">

             <IonText className = "ion-justify-content-center ion-align-items-center ion-margin">
                 <br />
                 <p>For working with multi-dimensional user data, I used dimension-reduction techniques like PCA (principal component analysis) and t-SNE (t-distributed 
              stochastic neighborhood embedding) to better visualize the data on a two-dimensional plane. The original project with
              CharacTour involved confidential datasets, so I recreated this analysis using the open-source Iris dataset in <code>R</code>. </p>
              
              <a href="https://cameronlowdata.shinyapps.io/IrisDimension/" target = "_blank" rel="noreferrer">Click here to see the dashboard on a new page.</a>
              <br />
              <IonButton className = "ion-button" onClick={() => setShowModal(false)}>Close</IonButton>

                <iframe src="https://cameronlowdata.shinyapps.io/IrisDimension/" name="iframe_a" height="500px" width="100%" title="Iframe Example"></iframe>
                
             <br />
             </IonText>
           
         
         </IonModal>

         <IonButton className = "ion-button"  onClick={() => setShowModal(true)}>Show</IonButton>
      </IonItem>
    )
}

export default WECharacTour;