import { IonIcon, IonItem, IonLabel, IonModal, IonButton, IonText } from '@ionic/react'
import { logoFacebook } from 'ionicons/icons';
import React, { useState } from 'react';
import './WEFacebook.css'

function WEFacebook() {
    const [showModal, setShowModal] = useState(false);

    return(
        <IonItem>
         <IonIcon icon={logoFacebook} slot = "start" className = "ion-icon"></IonIcon>
         <IonLabel>Data Science @ Facebook</IonLabel>

         <IonModal isOpen={showModal} className = "ion-modal-popup">
             <IonText className = "ion-justify-content-center ion-align-items-center ion-margin"> 
                 <p>During Summer 2021, I returned to Facebook as a data scientist intern. I am working on the Creator Commerce Ads Team within IG4B.</p>
                 <p>In Summer 2020, I was a data scientist intern on the Instagram Ecosystems team as part of Facebook University for Analytics. </p>
             </IonText>
            
                 <IonButton className = "ion-button" onClick={() => setShowModal(false)}>Close</IonButton>
         </IonModal>
         <IonButton className = "ion-button"  onClick={() => setShowModal(true)}>Show</IonButton>
      </IonItem>
    )
}

export default WEFacebook;